<template>
  <div>
        <!-- start page title -->
        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">Despesas Mensal</h4>
                     <div class="float-right">
                        <div class="dropdown">
                            <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><i class="mdi mdi-printer mr-1"></i> <span class="d-none d-sm-inline-block">Imprimir <i class="mdi mdi-chevron-down"></i></span></button>
                            <div class="dropdown-menu dropdown-menu-right dropdown-menu-md" style="">
                                <a class="dropdown-item" :href="imprimir()+'&estilo=resumido'" target="_blank">Imprimir Resumido</a>
                                <a class="dropdown-item" :href="imprimir()+'&estilo=detalhado'" target="_blank">Imprimir Detalhado</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="card">
            <div class="card-body">
                <div class="row" style="padding:10px">

                    <div class="col-sm-12 col-md-6">
                        <label class="col-form-label" >Período</label>                    
                        <div class=" mb-4">
                            <date-picker v-model="filtro.periodo" range :format="'DD/MM/YYYY'" range-separator=" até " :clearable="false" ></date-picker>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-4">
                        <label class="col-form-label" >Data de Referência</label>
                        <select class="form-control" v-model="filtro.data_referencia">
                            <option value="COMPETENCIA">Data de Competência</option>
                            <option value="CRIACAO">Data de Lançamento</option>
                            <!-- <option value="LANCAMENTO">Data de Lançamento</option> -->
                        </select>
                    </div>

                    <div class="col-sm-12 col-md-2">
                        <label class="col-form-label">&nbsp;</label>
                        <button type="button" class="btn btn-outline-primary waves-effect waves-light btn-block"
                            @click="this.buscarDados"
                        >
                            <i v-if="buscando" class="bx bx-loader bx-spin font-size-16 align-middle mr-2"></i> 
                            <i v-else class="bx bx bx-search  font-size-16 align-middle mr-2"></i> 
                            Buscar
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="card">
            <div class="card-body">
                <h4 class="card-title">Resumo</h4>
                <div class="row">
                    <div class="card-body">
                        <div class="row" style="padding:10px">
                            <div class="col-md-12">
                                <div class="table-responsive">
                                    <table class="table mb-0" >
                                        <thead>
                                            <tr>
                                                <th>Despesa</th>
                                                <th>Valor</th>
                                            </tr>
                                        </thead>
                                        <tbody >
                                            <tr v-for="despesa in despesas_com_lancamento" :key="despesa.id">
                                                <td>{{despesa.codigo}} - {{despesa.nome}}</td>
                                                <td>{{despesa.total  | currency($store.state.moeda)}}</td>
                                            </tr>       
                                            <tr style="font-size: 10pt">
                                                <th>Total</th>
                                                <th >{{despesa_total  | currency($store.state.moeda)}}</th>
                                            </tr>                             
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>                   

                </div>
            </div>
        </div>

        <div class="card">
            <div class="card-body">
                <div class="row" style="padding:10px">
                    <h4 class="card-title">Detalhado</h4>
                    <div class="col-md-12">
                        <div class="table-responsive">
                            <table class="table mb-0" >
                                <thead>
                                    <tr>
                                        <td></td>
                                        <th>Parceiro</th>
                                        <th>Histórico</th>
                                        <th>Competência</th>
                                        <th>Lançamento</th>
                                        <th>Valor</th>
                                    </tr>
                                </thead>
                                <tbody v-for="(despesa,index) in dados" :key="index">
                                    <tr 
                                        style="background-color: rgb(235 235 235);font-size: 10pt;font-weight: 700;">
                                        <td colspan="100000" >
                                            <div class="d-flex justify-content-between">
                                                <span>
                                                    <span v-for="ind in despesa.nivel" :key="ind">&emsp;</span>
                                                    {{despesa.codigo}} - {{despesa.nome}}
                                                </span>
                                                <span>
                                                    <span style="font-weight: 500; font-size: 8pt; margin-right: 30px" title="Valor Agrupado">
                                                        {{despesa.total_agrupado | currency($store.state.moeda)}}
                                                    </span>
                                                    
                                                    {{despesa.total | currency($store.state.moeda)}}

                                                </span>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr v-for="lancamento in despesa.lancamentos" :key="lancamento.id">
                                        <td></td>
                                        <td>{{lancamento.parceiro ? lancamento.parceiro.apelido : ''}}</td>
                                        <td>{{lancamento.historico}}</td>
                                        <td>{{lancamento.data_competencia | dateformat}}</td>
                                        <td>{{lancamento.data_lancamento | dateformat}}</td>
                                        <td>{{lancamento.valor  | currency($store.state.moeda)}}</td>
                                    </tr>                                    
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        
        

  </div>
</template>

<script>
import { CaixaService } from '../../../services'
const caixaService = new CaixaService();
import moment from 'moment'

export default {
    data(){
        return{
          
            timer:null,
            buscando: false,
        
            filtro:{
               periodo: [
                 moment().startOf('month').toDate(),
                 moment().endOf('month').toDate(), 
                ],
                data_referencia: 'COMPETENCIA'
            },
            dados: null
        }   
    },
    
    methods: {
        debounceBusca () {
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarDados();
            }, 800);
        },
        
        buscarDados () {
            if(this.buscando)
                return
            this.buscando = true;
           
            caixaService.despesasDetalhado(this.prepararFiltro()).then( response => {
                    this.dados = response.data
                }
            ).finally( () => this.buscando = false)
        },

        imprimir (){            
            let url = caixaService.link_despesas_detalhado(this.prepararFiltro());
            return url
        },

        prepararFiltro(){
            let obj = {...this.filtro};
            delete obj.periodo
            obj.data_inicial = moment(this.filtro.periodo[0]).format('DD-MM-YYYY')
            obj.data_final = moment(this.filtro.periodo[1]).format('DD-MM-YYYY')
            return obj;
        }
    },
    computed:{

        despesas_com_lancamento(){
            return this.dados.filter(x => x.total > 0);
        },

        despesa_total () {
            return this.despesas_com_lancamento.reduce( (a,c) => a + c.total, 0)
        }

    },
    
    mounted(){        
    
        setTimeout(() => {
            this.buscarDados();
        }, 200);
    },
};
</script>
